html {
  box-sizing: border-box;
  color: #102542;
  font-family: sans-serif;
  font-size: 24px; }

*,
*::before,
*::after {
  box-sizing: inherit;
  font-size: inherit;
  line-height: 1.4;
  margin: 0;
  padding: 0;
  position: relative; }

h1 {
  font-size: 42px; }

h2 {
  font-size: 30px; }

ul,
ol {
  list-style-position: inside; }

h3,
li,
p {
  margin-bottom: 0.5em; }

a {
  text-decoration: none; }

blockquote {
  border-left: 5px solid #f87060;
  padding-left: 40px;
  margin-bottom: 1.5em; }

hr {
  margin: 40px 0; }
